.sticky {
  position: sticky;
  top: 0; /* 클래스 sticky인 요소 안에서 top값이 0이되면 sticky를 적용 */
  background: #8a4baf;
}
header {
  position: sticky;
  padding-top: 32px;
  top: -32px;
  
  background: black;
  color: white;
  padding-left: 32px; 
  padding-right: 32px; 
  display: flex;
  justify-content: space-between;
  align-items: center;
}
body {
  background-color: beige;
}
